import { _decorator, Button, Component, instantiate,  Node, Prefab } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('shoot')
export class NewComponent extends Component {
    
    @property(Prefab)
    bulletPrefab: Prefab = null;


    @property(Button)
    shoot_btn: Button=null;

    start() {
        this.shoot_btn.node.on('click', this.onclickshoot,this);
    }

    update(deltaTime: number) {

    }

    onclickshoot(evt){
        console.log("按下射擊");
        let addbullet = instantiate(this.bulletPrefab);
        this.node.addChild(addbullet);

    }
}


